utton-color(~`colorPalette('@{color}', 7) `; transparent; transparent);\n      }\n      & when not (@theme = dark) {\n        .button-color(~`colorPalette('@{color}', 5) `; transparent; transparent);\n      }\n    }\n    & when not (@border = transparent) {\n      & when (@theme = dark) {\n        .button-color(\n          ~`colorPalette('@{color}', 7) `; transparent; ~`colorPalette('@{color}', 7) `\n        );\n      }\n      & when not (@theme = dark) {\n        .button-color(\n          ~`colorPalette('@{color}', 5) `; transparent; ~`colorPalette('@{color}', 5) `\n        );\n      }\n    }\n  }\n  &:active {\n    & when (@border = transparent) {\n      & when (@theme = dark) {\n        .button-color(~`colorPalette('@{color}', 5) `; transparent; transparent);\n      }\n      & when not (@theme = dark) {\n        .button-color(~`colorPalette('@{color}', 7) `; transparent; transparent);\n      }\n    }\n    & when not(@border = transparent) {\n      & when (@theme = dark) {\n        .button-color(\n          ~`colorPalette('@{color}', 5) `; transparent; ~`colorPalette('@{color}', 5) `\n        );\n      }\n      & when not (@theme = dark) {\n        .button-color(\n          ~`colorPalette('@{color}', 7) `; transparent; ~`colorPalette('@{color}', 7) `\n        );\n      }\n    }\n  }\n  .button-disabled();\n}\n.button-color(@color; @background; @border) {\n  color: @color;\n  background: @background;\n  border-color: @border; // a inside Button which only work in Chrome\n  // http://stackoverflow.com/a/17253457\n  > a:only-child {\n    color: currentColor;\n    &::after {\n      position: absolute;\n      top: 0;\n      right: 0;\n      bottom: 0;\n      left: 0;\n      background: transparent;\n      content: '';\n    }\n  }\n}\n.button-group-base(@btnClassName) {\n  position: relative;\n  display: inline-flex;\n  > .@{btnClassName},\n  > span > .@{btnClassName} {\n    position: relative;\n    &:hover,\n    &:focus,\n    &:active {\n      z-index: 2;\n    }\n    &